/* App.css */

body {
  font-family: Arial, sans-serif; /* Use a readable font */
  background-color: #f8f9fa; /* Set a light background color */
  margin: 0;
  padding: 0;
}

.container {
  max-width: 800px;
  margin: 50px auto; /* Center the container vertically */
  padding: 20px;
  background-color: #fff; /* Add a white background */
  border-radius: 10px; /* Add rounded corners */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Add a subtle shadow */
}




h1 {
  /* Other styles */
  animation: glow 2s ease-in-out infinite alternate; /* Apply glow animation */
  text-align: center; /* Center the text */
}

@keyframes glow {
  from {
    text-shadow: 0 0 10px #007bff, 0 0 20px #00bcd4, 0 0 30px #007bff; /* Add glow effect */
  }
  to {
    text-shadow: 0 0 20px #007bff, 0 0 40px #00bcd4, 0 0 60px #007bff; /* Increase glow effect */
  }
}





.description {
  font-size: 18px;
  color: #6c757d; /* Set text color */
  margin-bottom: 20px; /* Add space below the description */
}

.category {
  margin-bottom: 30px; /* Add space between categories */
}

.preview-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; /* Align items to the start */
  padding: 10px 20px; /* Reduce padding from top and bottom */
  background-color: #f8f9fa; /* Set background color */
  border-radius: 10px; /* Add rounded corners */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Add a subtle shadow */
  position: relative; /* Add position relative */
}

.preview {
  flex: 1;
  max-width: calc(100% - 200px); /* Limit width to leave space for button */
}

.link {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  position: absolute; /* Add position absolute */
  right: 0; /* Align to the right */
  top: 50%; /* Center vertically */
  transform: translateY(-50%); /* Adjust vertical position */
  font-weight: bold;
}

.link:hover {
  background-color: #0056b3;
  transform: translateY(-50%) translateX(2px); /* Move slightly right on hover */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Add a subtle shadow on hover */
}


.section {
  margin-bottom: 20px; /* Add space between sections */
}

.about-me, .education {
  border: 1px solid #ced4da; /* Add a light border */
  padding: 20px; /* Add padding */
  background-color: #f8f9fa; /* Set background color */
  border-radius: 10px; /* Add rounded corners */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Add a subtle shadow */
}

.about-me p, .education p {
  margin-bottom: 10px; /* Add space between paragraphs */
}

.about-me a {
  color: #007bff; /* Set link color */
  text-decoration: none; /* Remove underline */
}

.about-me a:hover {
  text-decoration: underline; /* Add underline on hover */
}




.section-heading {
  background-color: #000;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.section {
  margin-bottom: 30px; /* Add space between sections */
}

.about-me,
.education,
.deep-learning,
.machine-learning {
  border: 1px solid #ced4da; /* Add a light border */
  padding: 20px; /* Add padding */
  background-color: #f8f9fa; /* Set background color */
  border-radius: 10px; /* Add rounded corners */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Add a subtle shadow */
}

.about-me p,
.education p,
.deep-learning p,
.machine-learning p {
  margin-bottom: 10px; /* Add space between paragraphs */
}

.about-me a,
.education a,
.deep-learning a,
.machine-learning a {
  color: #007bff; /* Set link color */
  text-decoration: none; /* Remove underline */
}

.about-me a:hover,
.education a:hover,
.deep-learning a:hover,
.machine-learning a:hover {
  text-decoration: underline; /* Add underline on hover */
}

.profile-image {
  width: auto; /* Set the width of the image */
  height: 135px; /* Maintain aspect ratio */
  margin-right: 20px; /* Add space to the right of the image */
}

.info {
  display: flex; /* Use flexbox for layout */
  align-items: center; /* Align items vertically */
}

.details {
  flex: 1; /* Take remaining space */
}
